<template>
  <div class="request-details">
    <!-- HEADING SECTION -->
    <div class="heading section">
      <div class="container">
        <div>
          <p>
            {{ request.name }}
          </p>
          <span class="light-gray">
            {{ request.created_at }}
          </span>
        </div>

        <b-button
          size="sm"
          variant="primary"
          @click="
            () =>
              $router.push(
                localePath(`/dashboard/requests/${request.id}/offers`)
              )
          ">
          {{ $t('offer.viewRecievedOffers') }}
        </b-button>
      </div>
    </div>
    <!-- CARDS SECTION -->
    <div class="cards section">
      <div class="card">
        <svg-icon
          fill="#262D3D"
          name="calendar"
          height="24"
          width="24"
          class="mb-2 ml-2"></svg-icon>

        <div class="price-tag">
          <span>{{ request.duration }}</span>
          <p v-if="request.move_in_date">
            From,
            {{ $dateFns.format(new Date(request.move_in_date), 'dd MMM yyyy') }}
          </p>
        </div>
      </div>
      <div class="card">
        <svg-icon
          fill="#262D3D"
          name="dollar"
          height="24"
          width="24"
          class="mb-2"></svg-icon>

        <div class="price-tag">
          <span>{{ request.budget }}</span>
          {{ $t('common.egp') }}
          <p>
            {{
              request.term === 1
                ? $t('common.per_month')
                : $t('common.per_night')
            }}
          </p>
        </div>
      </div>
    </div>
    <!-- ADDRESS SECTION -->
    <div class="address section" v-if="request.locations">
      <div class="container">
        <div class="svg-icon">
          <svg-icon
            fill="#262D3D"
            name="MapPin"
            height="24"
            width="24"></svg-icon>
        </div>
        <div class="text">
          <p class="heading font-size-semi-large font-weight-bold mb-0">
            {{ $t('form.PropertyAddress') }}
          </p>
          <p class="address">
            <span
              v-for="(location, index) in Object.keys(request.locations)"
              :key="index"
              :class="index === 0 && 'font-weight-bold'">
              {{ request.locations[location].en }}
            </span>
          </p>
        </div>
      </div>
    </div>
    <!-- TERMS SECTION -->
    <div class="section">
      <div class="container">
        <p class="heading font-size-semi-large font-weight-bold">Terms</p>
        <span class="chip" v-for="term in request.conditions" :key="term.id">
          {{ term.name }}
        </span>
      </div>
    </div>
    <!-- FURNISHING STATUS SECTION -->
    <div class="section">
      <div class="container">
        <p class="heading font-size-semi-large font-weight-bold">
          {{ $t('property.furnishing_status') }}
        </p>
        <span
          class="chip"
          v-for="furnishType in request.furnishing_status"
          :key="furnishType">
          {{ furnishType }}
        </span>
      </div>
    </div>
    <!-- PROPERTY TYPES SECTION -->
    <div class="section">
      <div class="container">
        <p class="heading font-size-semi-large font-weight-bold">
          {{ $t('property.property_types') }}
        </p>
        <span
          class="chip"
          v-for="property_type in request.property_types"
          :key="property_type.id">
          {{ property_type.name_en }}
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'RequestDetails',
  data() {
    return {
      request: {},
    };
  },
  async created() {
    const { Data } = await this.$axios.$get(
      `/requests/${this.$route.params.requestId}`
    );
    this.request = Data;
  },
};
</script>

<style lang="scss" scoped>
.request-details {
  min-height: 100vh;
  background-color: #f7f9fa;

  .section {
    background-color: white;
    padding: 32px 0;
    margin-top: 18px;
  }

  .chip {
    display: inline-block;
    padding: 8px 12px;
    background-color: #e6f2ff;
    color: #0078ff;
    font-weight: bold;
    border-radius: 32px;
    margin: 0px 12px 12px 0;

    &:last-child {
      margin: 0;
    }
  }

  .heading {
    font-weight: bold;

    .container {
      display: flex;
      justify-content: space-between;
    }

    p {
      font-size: 18px;
      margin: 0;
    }

    .btn-primary {
      background-color: #e6f2ff;
      color: #0078ff;
      border: none;
      font-weight: bold;
      border-radius: 12px;
    }
  }

  .cards {
    display: flex;
    justify-content: space-around;

    .card {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      padding: 1rem;
      height: 160px;
      width: 176px;
      border: 1px solid #e3e5e5;
      border-radius: 32px;

      .price-tag {
        text-align: center;
        font-weight: bold;
        font-size: 16px;
        p {
          font-weight: 300;
          color: #48535b;
        }
      }
    }
  }

  .address {
    .container {
      display: flex;
      align-items: flex-start;

      .svg-icon {
        margin: 2px 16px 0 0;
      }
    }
  }
}

@media screen and (min-width: 768px) {
  .request-details {
    .cards {
      justify-content: center;

      .card {
        margin: 0 16px;
      }
    }
  }
}
</style>
